export default {
	data() {
		return {
			picker: this.$GambleList,
			TmpList: {},
			Wintype: 1,
			date: '2019-07-25',
			index: 0,
			money: 0,
			friend1: '',
			friend2: '',
			friend3: '',
			friend4: '',
			friends: '',
			address:''
		};
	},
	onLoad() {
		var now = new Date();
		var year = now.getFullYear(); //得到年份
		var month = now.getMonth() + 1; //得到月份
		var date = now.getDate(); //得到日期
		this.date = year + '-' + month + '-' + date;
	},
	methods: {
		PickerChange(e) {
			this.index = e.detail.value;
			
		},
		DateChange(e) {
			this.date = e.detail.value;
		//console.log('date改变==>>'+this.date )
		},
		ChangeType(e) {
			///console.log('首页==>>改变输赢类别==========');
			this.Wintype = e;
			
		},
		inputFriend1(e) {
			this.friend1 = e.detail.value;
			
		},
		inputAddress(e) {
			this.address= e.detail.value;
		
		},
		inputFriend2(e) {
			this.friend2 = e.detail.value;
		},
		inputFriend3(e) {
			this.friend3 = e.detail.value;
		},
		inputFriend4(e) {
			this.friend4 = e.detail.value;
		},
		inputMoney(e) {
			//
			this.money = e.detail.value;
		
		},
		go_save_list(e) {
			
			
			this.address = (this.address == null || this.address == undefined || this.address == '') ?"未填写":this.address
			this.friend1 = (this.friend1 == null || this.friend1 == undefined || this.friend1 == '') ?"未填写":this.friend1
			this.friend2 =(this.friend2 == null || this.friend2 == undefined || this.friend2 == '')?"未填写":this.friend2
			this.friend3 =(this.friend3 == null || this.friend3 == undefined || this.friend3 == '')?"未填写":this.friend3
			this.friend4 =(this.friend4 == null || this.friend4 == undefined || this.friend4 == '')?"未填写"	:this.friend4	
			
			this.friends = this.friend1 + "-" + this.friend2 + "-" + this.friend3 + "-" + this.friend4

			this.TmpList = {
				LstWIN: this.Wintype,
				LstDate: this.date,
				LstType: this.index,
				LstMoney: this.money,
				LstFriends: this.friends, 
				LstAddress:this.address
			}; 
			this.$WinList.unshift(this.TmpList);
			console.log('date结果==>>'+this.date )
			try {
				uni.setStorageSync('keyWinList', this.$WinList);
				uni.showToast({
					title: '保存数据成功！',
					icon: 'success',
					duration: 2000,
					success: function(res) {
						uni.navigateBack({
							delta: 1
						});
					}
				});
			} catch (e) {
				// error
				console.log(e);
				uni.showToast({
					title: '保存数据失败！',
					icon: 'success',
					duration: 2000
				});
			}
		},
		go_back(e) {
			uni.navigateBack({
				delta: 1
			});
		}
	}
}; 
